/*=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=
[Master Stylesheet]

Project:    HELSINKI
Version:    1.0
author:     myii-developer

[Table of contents]

1. Body
    2. wrap
        3. page-header
            4. leftside-header
            4. rightside-header
        3. page-body
            4. left-sidebar
            4. content
                5. content-header
            4. right-sidebar

=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/
html,
body {
  width: 100%;
  font-size: 13px;
}

body {
  background: #f7f7f7;
  margin: 0;
  color: #404040;
  font-family: "Open Sans", Arial, sans-serif;
  line-height: 22px;
  overflow-x: hidden;
  overflow-y: scroll;
}

a {
  color: #189279;
}

a:hover,
a:focus {
  color: #23d4af;
}

a:active {
  color: #0d5043;
}

a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
}

/* WRAPPER*/
/*================================================*/
.wrap {
  min-height: 100%;
  width: 100%;
}

/* PAGE HEADER*/
/*================================================*/
.page-header {
  height: 50px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding: 0;
  margin: 0;
  border: none;
}

/* PAGE BODY*/
/*================================================*/
.page-body {
  display: block;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  padding-top: 50px;
  float: left;
  position: relative;
}

/*CONTENT OF THE PAGE*/
/*================================================*/
.content {
  display: block;
  position: relative;
  vertical-align: top;
  margin-left: 220px;
  padding: 30px;
  background: #ececec;
  min-height: 100vh;
}
.content > .row + .row {
  padding-top: 15px;
}

.content-header {
  border-bottom: none;
  height: 40px;
  padding: 0;
}

/* LEFT SIDEBAR*/
/*================================================*/
.left-sidebar {
  display: block;
  height: 100%;
  float: left;
  position: relative;
  width: 220px;
  z-index: 1030;
}

/* Left-sidebar COLLAPSE*/
/*================================================*/
html.left-sidebar-collapsed .content {
  margin-left: 60px;
}
html.left-sidebar-collapsed .left-sidebar {
  width: 60px;
}

/* RIGHT SIDEBAR*/
/*================================================*/
.right-sidebar {
  display: block;
  height: 100%;
  float: right;
  position: relative;
  width: 240px;
  z-index: 1020;
  position: fixed;
  bottom: 0;
  right: 0;
  padding-bottom: 50px;
  top: 50px;
  margin-right: -240px;
  transition-property: margin;
  transition-duration: 0.25s;
  transition-timing-function: ease-out;
  transition-delay: 0;
}
.right-sidebar .nano-content {
  padding-bottom: 50px;
}

/* right-sidebar OPENED*/
/*================================================*/
html.right-sidebar-opened .right-sidebar {
  margin-right: 0;
}

/* SCROLL TO TOP*/
/*================================================*/
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 40px;
  z-index: 1010;
  height: 40px;
  display: none;
  border-radius: 50%;
  text-align: center;
  background: #189279;
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, 0.53);
}
.scroll-to-top i {
  color: #ffffff;
  font-size: 20px;
  width: 40px;
  height: 40px;
  padding: 10px;
