/* COMPONENTS - NANO-SCROLL*/
/*================================================*/
.nano {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.nano > .nano-content {
  position: absolute;
  overflow: scroll;
  overflow-x: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.nano > .nano-content:focus {
  outline: thin dotted;
}
.nano > .nano-content::-webkit-scrollbar {
  display: none;
}
.nano.has-scrollbar > .nano-content::-webkit-scrollbar {
  display: block;
}
.nano > .nano-pane {
  display: block !important;
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  width: 5px;
  right: 0;
  top: 0;
  bottom: 0;
  visibility: hidden\9 ;
  /* Target only IE7 and IE8 with this hack */
  opacity: 0.01;
  transition: 0.2s;
  border-radius: 5px;
}
.nano > .nano-pane > .nano-slider {
  background: #189279;
  position: relative;
  margin: 0 1px;
  border-radius: 3px;
}
.nano:hover > .nano-pane {
  visibility: visible\9 ;
  /* Target only IE7 and IE8 with this hack */
  opacity: 0.99;
}
.nano .nano-pane.active, .nano .nano-pane.flashed {
  visibility: visible\9 ;
  /* Target only IE7 and IE8 with this hack */
  opacity: 0.99;
}

