/* COMPONENT - HAMBURGER ICONS */
/*================================================*/
.c-hamburger {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 60px;
  height: 40px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  transition: background 0.5s;
}

.c-hamburger:focus {
  outline: none;
}

.c-hamburger span {
  display: block;
  position: absolute;
  top: 18.5px;
  left: 18px;
  right: 18px;
  height: 3px;
  background: white;
}

.c-hamburger span::before,
.c-hamburger span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  content: "";
}

.c-hamburger--htla span {
  transition: transform 0.5s;
}

.c-hamburger--htla span::before {
  transform-origin: top right;
  transition: transform 0.5s, width 0.5s, top 0.5s;
}

.c-hamburger--htla span::after {
  transform-origin: bottom right;
  transition: transform 0.5s, width 0.5s, bottom 0.5s;
}

.c-hamburger--htla span {
  transform: rotate(180deg);
}

.c-hamburger--htla span::before,
.c-hamburger--htla span::after {
  width: 50%;
}

.c-hamburger--htla span::before {
  top: 0;
  transform: translateX(15px) translateY(1.5px) rotate(45deg);
}

.c-hamburger--htla span::after {
  bottom: 0;
  transform: translateX(15px) translateY(-1.5px) rotate(-45deg);
}

/* active state, i.e. menu open */
html.left-sidebar-collapsed .c-hamburger--htla span {
  transform: none;
}
html.left-sidebar-collapsed .c-hamburger--htla span::before,
html.left-sidebar-collapsed .c-hamburger--htla span::after {
  width: 100%;
}
html.left-sidebar-collapsed .c-hamburger--htla span::before {
  top: 7px;
  transform: none;
}
html.left-sidebar-collapsed .c-hamburger--htla span::after {
  top: -7px;
  transform: none;
}

html.left-sidebar-top .c-hamburger span {
  top: 23.5px;
}

